.hero {
  background: no-repeat center/cover url('../img/bg-hero.png');
  background-color: $dark-red;
  padding: 30px 0 47px 0;

  @include tablets {
    padding: 40px 0 78px 0;
  }

  @include phones {
    padding: 20px 0 80px 0;
  }
}

.hero__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 0.8;

  @include tablets {
    margin-bottom: 21px;
  }

  @include phones {
    margin-bottom: 75px;
  }

  &.container {
    @include phones {
      min-width: 90%;
    }
  }

  a {
    &:hover {
      opacity: 1;
    }

    svg {
      @include tablets {
        width: 82px;
        height: 20px;
      }

      @include phones {
        width: 68px;
        height: 15px;
      }
    }
  }

  a:last-child {
    font-family: $bebas;
    font-size: 26px;
    color: $white;
    letter-spacing: 0.06rem;

    @include tablets {
      font-size: 24px;
      letter-spacing: 0.04rem;
    }

    @include phones {
      font-size: 20px;
      letter-spacing: 0.03rem;
    }
  }
}

.hero__main {
  display: flex;
  justify-content: space-between;
}

.hero__welcome {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-end;
  width: 34.2%;
  color: $white;
  margin-bottom: 46px;

  @include tablets {
    width: 48%;
    margin-bottom: 27px;
  }

  @include phones {
    width: 100%;
    margin-bottom: 0;
  }

  span {
    font-size: 49px;
    font-family: $bebas;
    font-weight: 400;
    letter-spacing: 0.09rem;
    text-transform: uppercase;
    margin-bottom: 10px;

    @include tablets {
      font-size: 34px;
      margin-bottom: 5px;
    }

    @include phones {
      font-size: 28px;
      margin-bottom: 10px;
    }
  }

  a {
    display: inline-flex;
    padding: 20px 54px;
    border: 4px solid $white;
    font-size: 12px;
    font-weight: 700;
    letter-spacing: 0.05em;
    color: $white;
    text-transform: uppercase;

    @include tablets {
      padding: 20px 30px;
    }

    @include phones {
      padding: 20px 82px;
    }
  }
}

.hero__title {
  position: relative;
  display: flex;
  margin-bottom: 57px;
  text-align: right;
  width: 100%;

  @include tablets {
    margin-bottom: 39px;
  }

  @include phones {
    margin-bottom: 41px;
  }

  h1 {
    margin: 0;
    font-size: 109px;
    font-family: $bebas;
    font-weight: 700;
    line-height: 109px;
    text-transform: uppercase;
    letter-spacing: 0.2rem;

    @include tablets {
      font-size: 86px;
      line-height: 86px;
      letter-spacing: 0.17rem;
    }

    @include phones {
      font-size: 78px;
      line-height: 78px;
      letter-spacing: 0.03em;
    }
  }

  ul {
    position: absolute;
    bottom: 25px;
    left: 20px;
    list-style: none;
    margin: 0;
    padding: 0;
    font-size: 14px;

    @include tablets {
      bottom: 15px;
      left: 0;
    }

    @include phones {
      bottom: 10px;
    }


    li {
      padding: 3px 0;
    }
  }
}

.hero__banner {
  width: 53.5%;
  height: 100%;

  @include tablets {
    width: 48%;
  }

  @include phones {
    display: none;
  }

  img {
    @include tablets {
      width: 324px;
      height: 390px;
    }
  }
}

.hero__socials {
  display: flex;
  align-items: flex-end;
  margin-bottom: 4%;
  width: 1%;

  @include tablets {
    display: none;
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  li {
    margin-bottom: 15px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  a {
    font-size: 12px;
    text-transform: uppercase;
    color: $white;
  }
}
